import {
  GET_INDEX_OHLC,
  GET_INDICES,
  GET_OVERVIEW,
  GET_PERFORMANCE,
  GET_GAINERS_MARGIN,
  GET_LOSERS_VALUATION,
  GET_LOSERS_MARGIN,
  GET_SECTORS,
  GET_INDUSTRIES,
  GET_SECTORS_PERFORMANCE,
  GET_INDUSTRY_PERFORMANCE,
  GET_GAINERS_VALUATION,
  GET_INDICES_KAFKA
} from "./Types";

import axios from "axios";
import { stopLoading, startLoading } from "./LoadingAction";
const url = "https://node-stockbazaar.herokuapp.com/api/stocks";

export const getAllIndices = () => dispatch => {
  dispatch(startLoading());
  return axios
    .get(url + "/indices", {})
    .then(res => {
      dispatch(stopLoading());
      dispatch({
        type: GET_INDICES,
        payload: res.data.data
      });
      return true;
    })
    .catch(err => {
      console.log(err);
    });
};
