import React, { Component } from "react";
import { Chart, Radar } from "react-chartjs-2";
Chart.defaults.global.legend.display = false;

export default class FilteredTickerCard extends Component {
  state = {
    data: {
      type: "radar",
      labels: ["EV/EBITDA", "Growth", "MKT CAP", "Net Profit", "Assets"],
      datasets: [
        {
          data: this.props.tickerData.dataPointsArray
            ? this.props.tickerData.dataPointsArray
            : [],
          borderWidth: 2,
          borderJoinStyle: "round",
          pointRadius: 0
        }
      ]
    }
  };
  // returns gradient color to the data
  setGradientColor = (canvas, hslValue) => {
    const ctx = canvas.getContext("2d");
    const gradient = ctx.createLinearGradient(100, 200, 3500, 100);
    gradient.addColorStop(0, hslValue);
    gradient.addColorStop(0.9, "rgba(10,10,10,0.3)");
    return gradient;
  };
  // this function updates the background color, border color and border color
  // of point while hovering on it, the color changes when points are dragged.
  getChart = canvas => {
    let data = this.state.data;
    let sum = 0;
    for (let i = 0; i < data.datasets[0].data.length; i++) {
      sum += data.datasets[0].data[i];
    }
    // calculates the average and get the color range for hsl value from 0 to 155
    sum = ((sum / 5) * 155) / 100;
    if (sum <= 31) {
      sum = 0;
    } else {
      sum -= 31;
    }
    let hslValue = "hsl(" + sum + ",87%,57%,0.9)";
    if (data.datasets) {
      data.datasets[0].backgroundColor = this.setGradientColor(
        canvas,
        hslValue
      );
      data.datasets[0].borderColor = "hsl(" + sum + ",87%,57%,1)";
      data.datasets[0].pointHoverBorderColor = "hsl(" + sum + ",87%,57%,1)";
    }
    return data;
  };
  render() {
    return (
      <div
        className="filtered-grid-item"
        onClick={() => {
          this.props.history.push(
            "/stockspecific/" + this.props.tickerData.ticker_data.ticker_id
          );
        }}
      >
        {/* <Radar
          data={this.getChart}
          options={{
            responsive: true,
            scale: {
              pointLabels: {
                fontColor: "#ffffff",
                fontFamily: "'Work Sans',sans-serif",
                fontSize: "0"
              },
              angleLines: {
                display: true,
                color: "#4b527b"
              },
              gridLines: {
                color: "#4b527b"
              },
              ticks: {
                display: false,
                max: 100,
                min: 0,
                stepSize: 20
              }
            }
          }}
        /> */}
        <div style={{ display: "flex", justifyContent: "space-between" }}>
          <p style={{ fontSize: "20px" }}>
            {this.props.tickerData.ticker_data.ticker_name}
          </p>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "flex-end"
            }}
          >
            <span
              style={{
                color:
                  this.props.tickerData.ticker_data.change > 0
                    ? "var(--green)"
                    : "var(--red)"
              }}
            >
              {this.props.tickerData.ticker_data.change}
            </span>{" "}
            <span
              style={{
                color:
                  this.props.tickerData.ticker_data.changePercentage > 0
                    ? "var(--green)"
                    : "var(--red)"
              }}
            >
              ({this.props.tickerData.ticker_data.changePercentage}%)
            </span>
          </div>
        </div>
        <p style={{ marginTop: "16px" }}>
          Sector: {this.props.tickerData.sector}
        </p>
        <p>Industry: {this.props.tickerData.industry}</p>
      </div>
    );
  }
}
