import {
  GET_STOCKCHART,
  GET_STOCK_DETAILS,
  GET_STOCK_DETAILS_2,
  GET_FINANCIAL_REPORTS,
  GET_FINANCIAL_REPORTS_2,
  GET_MONTECARLO_GRAPH,
  GET_ALL_REPORTS,
  GET_EARNINGSVSREVENUE,
  GET_PROFITLOSS,
  GET_ASSETS,
  GET_LIABILITIES,
  GET_CASHFLOW
} from "./Types";

import axios from "axios";
const url = "https://node-stockbazaar.herokuapp.com/api/stockspecific";
const ploturl = "https://python-stockbazaar.herokuapp.com/";

export const getStockChart = ticker_id => dispatch => {
  return axios
    .get(ploturl + "stockchart/" + ticker_id)
    .then(res => {
      // console.log(ticker_id);
      dispatch({
        type: GET_STOCKCHART,
        payload: res.data
      });
      // console.log(res.data);
    })
    .catch(err => {
      console.log(err);
    });
};

export const getStockDetails = ticker_id => dispatch => {
  return axios
    .get(url + "/getstockdetails/" + ticker_id)
    .then(res => {
      // console.log(ticker_id);
      dispatch({
        type: GET_STOCK_DETAILS,
        payload: res.data.data
      });
      // console.log(res.data.data);
    })
    .catch(err => {
      console.log(err);
    });
};

